#app2 {
    border: 1px solid blue;
}

#app2 .tab-bar {
    display: flex;
}

#app2 .tab-bar > li {
    width: 50%;
}

#app2 .tab-bar > li.selected {
    background: pink;
    color: black;
}

#app2 .tab-content {

}

#app2 .tab-content > li {
    display: none;
}

#app2 .tab-content > li.active {
    display: block;
}
